
.MoveLogs {
  /* border: 1px dashed tomato; */
  /* height: 175px; */
  /* flex: 1; */
  -webkit-box-shadow: 5px 5px 30px rgba(0,0,0,.1);
          box-shadow: 5px 5px 30px rgba(0,0,0,.1);
  border-radius: 10px;
  border: 1px solid #ecf0f1;
  border-color: #bec3c7;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.MoveLogs p {
  margin: 0px;
  padding: 5px;
  -webkit-box-shadow: 0px 5px 10px rgba(0,0,0,.1);
          box-shadow: 0px 5px 10px rgba(0,0,0,.1);
}

.MoveLogs .logs {
  -ms-flex: 1 1;
      flex: 1 1;
  overflow-y: auto;
  padding-bottom: 15px;
}

.log-line {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0px 15px;
  border-top: 1px dashed #ecf0f1;
}

.log-line span {
  -ms-flex: 1 1;
      flex: 1 1;
}

@media only screen and (max-width: 768px) {
  .MoveLogs {
    margin-right: 4px;
    height: auto;
  }
}



