.modeSelector {
  /* border: 1px solid red; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 50px;
  padding: 0px 40px;
}

.modeSelector button {
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 1em;
  padding: 14px 0px;
  min-height: 75px;
  border: 1px solid #bec3c7;
  background-color: #fff;
  cursor: pointer;
  -webkit-transition: .1s linear;
  -o-transition: .1s linear;
  transition: .1s linear;
}

.modeSelector button:hover {
  background-color: rgba(45,204,112,.3);
  border: 1px solid rgba(45,204,112,1);
  -webkit-box-shadow: inset 0px 0px 0px 2px rgba(45,204,112,1);
          box-shadow: inset 0px 0px 0px 2px rgba(45,204,112,1);
  text-decoration: underline;
}

.modeSelector button:nth-child(1) {
  border-radius: 30px 0px 0px 30px;
}

.modeSelector button:nth-child(2) {
  border-radius: 0px 30px 30px 0px;
}



